// Status - closed(open for responsive view)
// Component  - ViewDemandDetails
// DESCRIPTION - This component is to show demand of SAF applications

import { useState, useEffect } from 'react'
import { BiRightArrowAlt } from 'react-icons/bi'
import rupee from '../../../src/Components/Media/rupee.png'
import brief from '../../../src/Components/Media/brief.png'
import axios from 'axios'
import { useNavigate, useParams } from 'react-router-dom'
import CitizenApplyApiList from '../../Components/CitizenApplyApiList'
import TopTabs from './CitizenSafForm/SafFormReview/TopTabs'
import ApiHeader from '../../Components/ApiList/ApiHeader'
import { AiOutlineInfoCircle } from 'react-icons/ai'
import CommonModal from '../../Components/GlobalData/CommonModal'
import ServerErrorCard from '../AdvertisementModule/ServerErrorCard'
import { indianAmount, indianDate, nullToNA, nullToZero } from '../../Components/Common/PowerUps/PowerupFunctions'
import BrandLoader from '../../Components/Common/BrandLoader'
import { FiAlertCircle } from 'react-icons/fi'
import Modal from 'react-modal';
import { buttonActionSolid } from '../../Components/Common/Styles/Styles'
import AxiosInterceptors from '../../Components/GlobalData/AxiosInterceptors'
const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        backgroundColor: 'white',
        border: 'none'
    },
};
Modal.setAppElement('#root');

function ViewDemandDetails(props) {
    const [demandDetail, setdemandDetail] = useState()
    const [safNo, setsafNo] = useState('')
    const [isLoading, setisLoading] = useState(false);
    const [erroState, seterroState] = useState(false);
    const [modalIsOpen, setIsOpen] = useState(false);


    const { id } = useParams()
    const { api_getsafDemandById } = CitizenApplyApiList();
    const navigate = useNavigate()

    function closeModal() {
        setIsOpen(false);
    }

    // FUNCTION TO FETCH SAF DEMAND DETAILS
    const fetchDemandDetail = () => {
        seterroState(false)
        setisLoading(true)
        AxiosInterceptors.post(api_getsafDemandById,
            {
                id: id
            },
            ApiHeader())
            .then(function (response) {
                console.log('view demand for my saf..', response?.data?.data)
                if (response?.data?.status) {
                    setsafNo(response?.data?.safNo)
                    setdemandDetail(response?.data?.data)
                    showPopup(response?.data?.data?.paymentStatus, response?.data?.data?.basicDetails?.doc_upload_status)
                }
                setisLoading(false)
            })
            .catch(function (error) {
                console.log('==at saf separate error...', error)
                seterroState(true)
                setisLoading(false)
            })
    }

    const showPopup = (paymentStatus, docUploadStatus) => {
        if (paymentStatus == 0 && docUploadStatus == 0) {
            console.log('inside useffect')
            setIsOpen(true)
        }
    }
    useEffect(() => {
        fetchDemandDetail()

    }, [])

    if (isLoading) {
        return (
            <>
                <BrandLoader />
                <div className='min-h-screen'></div>
            </>
        )
    }
    if (erroState) {
        return (
            // <CommonModal>
            <ServerErrorCard title="Server is busy" desc="Server is too busy to respond. Please try again later." buttonText="View Dashboard" buttonUrl="/propertyDashboard" />
            // </CommonModal>
        )
    }





    return (
        <>
            <div className='w-full mx-auto'>
                <div className=''>
                    <TopTabs payButton={demandDetail?.paymentStatus == 0 && demandDetail?.basicDetails?.doc_upload_status == 1} payableAmount={demandDetail?.amounts?.payableAmount} title={`Demand Details`} type="application" id={id} safNo={safNo} active="demand" />
                    {
                        demandDetail?.paymentStatus === 1 &&
                        <div className=''>
                            <span className='bg-red-400 text px-4 py-1 justify-center items-center font-semibold text-white shadow-xl'>All Demand Paid</span>
                        </div>
                    }
                </div>

                {demandDetail?.paymentStatus == 0 && demandDetail?.basicDetails?.doc_upload_status == 0 && <div className="">
                    <div className="items-center text-yellow-600"><AiOutlineInfoCircle className="inline mr-2" />Upload all your property related documents first then pay your property tax to send your application for verification</div>

                </div>}
                <div className='w-full bg-white shadow-xl mb-6'>
                    <div className='py-6 mt-2 rounded-lg shadow-lg p-4'>
                        <div className="flex flex-col md:flex-row space-y-2 md:space-x-5 pl-4 ">

                            <div className='flex-1'>
                                <div className='font-bold text-sm'>{nullToNA(demandDetail?.basicDetails?.saf_no)}</div>
                                <div className='text-gray-500 text-xs'>Application No.(Saf No)</div>
                            </div>
                            <div className='flex-1'>
                                <div className='font-bold text-sm'>{nullToNA(demandDetail?.basicDetails?.old_ward_no)}</div>
                                <div className='text-gray-500 text-xs'>Ward No.</div>
                            </div>
                            <div className='flex-1'>
                                <div className='font-semibold text-lg'>{nullToNA(demandDetail?.basicDetails?.old_ward_no)}</div>
                                <div className='text-gray-500 text-xs'>New Ward No</div>
                            </div>
                            <div className='flex-1'>
                                <div className='font-semibold text-md'>{nullToNA(demandDetail?.basicDetails?.ownership_type)}</div>
                                <div className='text-gray-500 text-xs'>Ownership Type</div>
                            </div>
                            <div className='flex-1'>
                                <div className='font-bold text-sm'>{nullToNA(demandDetail?.basicDetails?.property_type)}</div>
                                <div className='text-gray-500 text-xs'>Property Type</div>
                            </div>

                        </div>

                        <div className="flex flex-col md:flex-row space-y-2 md:space-x-10  pl-4 mt-4">
                            <div className='flex-1'>
                                <div className='font-bold text-sm'>{nullToNA(demandDetail?.basicDetails?.holding_type)}</div>
                                <div className='text-gray-500 text-xs'>Holding Type</div>
                            </div>
                            <div className='flex-1'>
                                <div className='font-bold text-sm'>{nullToNA(demandDetail?.basicDetails?.is_mobile_tower)}</div>
                                <div className='text-gray-500 text-xs'>Property has Mobile Tower(s) ?</div>
                            </div>
                            <div className='flex-1'>
                                <div className='font-semibold text-md'>{nullToNA(demandDetail?.basicDetails?.is_hoarding_board)} </div>
                                <div className='text-gray-500 text-xs'>Property has Hoarding Board(s) ?</div>
                            </div>
                            <div className='flex-1'>
                                <div className='font-semibold text-md'>{nullToNA(demandDetail?.basicDetails?.is_petrol_pump)}</div>
                                <div className='text-gray-500 text-xs'>Is property a Petrol Pump ?</div>
                            </div>
                            <div className='flex-1'>
                                <div className='font-bold text-sm' >{nullToNA(demandDetail?.basicDetails?.is_water_harvesting)}</div>
                                <div className='text-gray-500 text-xs'>Rainwater harvesting provision ?</div>
                            </div>
                        </div>
                    </div>

                </div>

                <div className=''>

                    {/* {
                        demandDetail?.paymentStatus == 1 &&
                        <div className='w-full mt-20 text-center'>
                            <span className='text-3xl text-red-500 border border-red-500 font-semibold px-4 py-2'>No Dues Found !</span>
                        </div>
                    } */}

                    <>
                        <div className='mt-10'>
                            <h1 className='px-1 font-semibold font-serif text-xs'><img src={rupee} alt="pin" className='w-5 inline' /> Tax Details</h1>
                            <div className='flex font-mono text-xs py-2 px-1 text-gray-900'>
                                <div className='flex-initial px-2 font-bold'>Total Payable Amount</div>
                                <div className='flex-initial px-2'>= </div>
                                <div className='flex-initial px-2 bg-gray-100 rounded-lg'> ( Tax Amount</div>
                                <div className='flex-initial px-2'>+</div>
                                <div className='flex-initial px-2 bg-gray-100 rounded-lg'>Late Assessment Penalty</div>
                                <div className='flex-initial px-2'>+</div>
                                <div className='flex-initial px-2 bg-gray-100 rounded-lg'>1% Penalty )</div>
                                <div className='flex-initial px-2'>-</div>
                                <div className='flex-initial px-2 bg-gray-100 rounded-lg'> ( Rebate</div>
                                <div className='flex-initial px-2'>+</div>
                                <div className='flex-initial px-2 bg-gray-100 rounded-lg'>Special Rebate )</div>
                            </div>

                            {/* <table className='min-w-full leading-normal mt-2'>
                                    <thead className='font-bold text-left text-sm bg-white text-gray-600'>
                                        <tr>
                                            <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Tax Amount (₹)</th>
                                            <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Late Assessment Penalty (₹)</th>
                                            <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">1% Penalty (₹)</th>
                                            <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Rebate( {demandDetail?.amounts?.rebatePerc}% in ₹)</th>
                                            <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Special Rebate( {demandDetail?.amounts?.specialRebatePerc}% in ₹)</th>
                                            <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Payable Amount (₹)</th>

                                        </tr>
                                    </thead>
                                    <tbody className="text-sm">
                                        <tr className="bg-white shadow-lg border-b border-gray-200">
                                            <td className="px-2 py-2 text-sm text-left">{nullToZero(demandDetail?.amounts?.totalTax)}</td>
                                            <td className="px-2 py-2 text-sm text-left">{nullToZero(demandDetail?.amounts?.lateAssessmentPenalty)}</td>
                                            <td className="px-2 py-2 text-sm text-left">{nullToZero(demandDetail?.amounts?.totalOnePercPenalty)}</td>
                                            <td className="px-2 py-2 text-sm text-left">{nullToZero(demandDetail?.amounts?.rebateAmt)}</td>
                                            <td className="px-2 py-2 text-sm text-left">{nullToZero(demandDetail?.amounts?.specialRebateAmt)}</td>
                                            <td className="px-2 py-2 text-sm text-left">{nullToZero(demandDetail?.amounts?.payableAmount)}</td>
                                        </tr>

                                    </tbody>
                                </table> */}
                            <table className='min-w-full leading-normal mt-2'>
                                <thead className='font-bold text-left text-sm bg-white text-gray-600'>
                                    <tr>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">#</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">ARV</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Effect From</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Holding Tax</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Water Tax</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Conservancy/Latrine Tax</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Education Cess</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Health Cess</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">RWH Penalty</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Quarterly Tax</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Ruleset</th>


                                    </tr>
                                </thead>
                                <tbody className="text-sm">
                                    {
                                        demandDetail?.taxDetails?.map((data, index) => (
                                            <tr className="bg-white shadow-lg border-b border-gray-200">
                                                <td className="px-2 py-2 text-sm text-left">{index + 1}</td>
                                                <td className="px-2 py-2 text-sm text-left">{nullToZero(data?.arv)}</td>
                                                <td className="px-2 py-2 text-sm text-left">{nullToNA(data?.fyear)}</td>
                                                <td className="px-2 py-2 text-sm text-left">{nullToZero(data?.holding_tax)}</td>
                                                <td className="px-2 py-2 text-sm text-left">{nullToZero(data?.water_tax)}</td>
                                                <td className="px-2 py-2 text-sm text-left">{nullToZero(data?.latrine_tax)}</td>
                                                <td className="px-2 py-2 text-sm text-left">{nullToZero(data?.education_cess)}</td>
                                                <td className="px-2 py-2 text-sm text-left">{nullToZero(data?.health_cess)}</td>
                                                <td className="px-2 py-2 text-sm text-left">{nullToZero(data?.additional_tax)}</td>
                                                <td className="px-2 py-2 text-sm text-left">{nullToZero(data?.amount)}</td>
                                                <td className={`px-2 py-2 text-sm text-left  `}> <span className={`${data?.ruleSet === 'RuleSet3' ? 'bg-green-100' : 'bg-red-100'} px-2 py-1 rounded-lg`}>{data?.ruleSet === 'RuleSet3' ? 'Current' : 'Old'}</span> </td>
                                            </tr>
                                        ))

                                    }

                                </tbody>
                            </table>
                        </div>


                        {/* // REABATE DESCRIPTION */}
                        {demandDetail?.amounts?.rebates?.length > 1 && <>
                            <div className='mt-10 text-md font-semibold'>Rebate Description</div>
                            <table className='min-w-full leading-normal mt-2'>
                                <thead className='font-bold text-left text-sm bg-white text-gray-600'>
                                    <tr>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">#</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Rebate Type</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">percent(%)</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Amount (₹)</th>


                                    </tr>
                                </thead>
                                <tbody className="text-sm">

                                    {demandDetail?.amounts?.rebates?.map((data, index) => (
                                        <tr className="bg-white shadow-lg border-b border-gray-200">
                                            <td className="px-2 py-2 text-sm text-left">{index + 1}</td>
                                            <td className="px-2 py-2 text-sm text-left">{nullToNA(data?.keyString)}</td>
                                            <td className="px-2 py-2 text-sm text-left">{nullToZero(data?.rebatePerc)}%</td>
                                            <td className="px-2 py-2 text-sm text-left">{nullToZero(data?.rebateAmount)}</td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </>
                        }


                        {/* due detail list */}
                        <div className='mt-10'>
                            {/* RULESET-1 */}
                            {
                                demandDetail?.details &&
                                <div>
                                    <h1 className='px-1 font-semibold font-serif text-md mt-10'><img src={brief} alt="pin" className='w-5 inline' /> Tax Description of Annual Rental Value - As Per Old Rule (Effect Upto 31-03-2016)</h1>
                                    <div className='flex font-mono text-xs py-2 px-1 text-gray-900'>
                                        <div className='flex-initial px-2 font-bold'>Annual Rental Value (ARV)</div>
                                        <div className='flex-initial px-2'>=</div>
                                        <div className='flex-initial px-2 bg-gray-100 rounded-lg'>Builtup Area</div>
                                        <div className='flex-initial px-2'>x</div>
                                        <div className='flex-initial px-2 bg-gray-100 rounded-lg'>Rental Rate</div>
                                    </div>
                                    <table className='min-w-full leading-normal mt-2'>
                                        <thead className={`font-bold text-left text-sm ${demandDetail?.paymentStatus === 1 ? 'bg-red-100' : 'bg-white'} text-gray-600`}>

                                            <tr>
                                                <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">#</th>
                                                <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">ARV</th>
                                                <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Quarter</th>
                                                <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Quarter / Year</th>
                                                <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Holding Tax (₹)</th>
                                                <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Water Tax (₹)</th>
                                                <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Latrine/Conservancy Tax (₹)</th>
                                                <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Education Cess (₹)</th>
                                                <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Health Cess (₹)</th>
                                                <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Additional Tax</th>
                                                <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Quarterly Tax (₹)</th>
                                                <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Adjuted Amount</th>
                                                <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Balance</th>
                                                <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Due Date</th>
                                                <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">1% penalty (₹)</th>

                                            </tr>
                                        </thead>
                                        <tbody className="text-sm">

                                            <>
                                                {demandDetail.details?.map((items, index) => (
                                                    <tr className={`${demandDetail?.paymentStatus === 1 ? 'bg-red-100' : 'bg-white'} shadow-lg border-b border-gray-200`}>
                                                        <td className="px-2 py-2 text-sm text-left">{index + 1}</td>
                                                        <td className="px-2 py-2 text-sm text-left">{nullToNA(items?.arv)}</td>
                                                        <td className="px-2 py-2 text-sm text-left">{nullToNA(items?.qtr)}</td>
                                                        <td className="px-2 py-2 text-sm text-left">{nullToNA(items?.fyear)}</td>
                                                        <td className="px-2 py-2 text-sm text-left">{nullToNA(items?.holding_tax)}</td>
                                                        <td className="px-2 py-2 text-sm text-left">{nullToNA(items?.water_tax)}</td>
                                                        <td className="px-2 py-2 text-sm text-left">{nullToNA(items?.latrine_tax)}</td>
                                                        <td className="px-2 py-2 text-sm text-left">{nullToNA(items?.education_cess)}</td>
                                                        <td className="px-2 py-2 text-sm text-left">{nullToNA(items?.health_cess)}</td>
                                                        <td className="px-2 py-2 text-sm text-left">{nullToNA(items?.additional_tax)}</td>
                                                        <td className="px-2 py-2 text-sm text-left">{nullToNA(items?.amount)}</td>
                                                        <td className="px-2 py-2 text-sm text-left">{nullToNA(items?.adjust_amount)}</td>
                                                        <td className="px-2 py-2 text-sm text-left">{nullToNA(items?.balance)}</td>
                                                        <td className="px-2 py-2 text-sm text-left">{indianDate(items?.due_date)}</td>
                                                        <td className="px-2 py-2 text-sm text-left">{nullToNA(items?.onePercPenaltyTax)}({nullToNA(items?.onePercPenalty)}%)</td>
                                                    </tr>
                                                ))}

                                            </>

                                        </tbody>
                                    </table>
                                </div>
                            }



                            {demandDetail?.paymentStatus !== 1 && <div className='mt-10 flex'>
                                <>
                                    <div className='text-left flex-1'>
                                        <div>
                                            <span className='font-semibold text-gray-600 text-xl'>Total Payable Amount  </span><span className='text-3xl font-bold ml-20'>{indianAmount(demandDetail?.amounts?.payableAmount)}</span>
                                        </div>
                                    </div>
                                    <div className='text-right flex-1'>
                                        {demandDetail?.paymentStatus == 0 && demandDetail?.basicDetails?.doc_upload_status == 1 && <button onClick={() => navigate(`/property-payment/${id}/saf`)} type="submit" className={buttonActionSolid}>{demandDetail?.amounts?.isPayable ? "Pay Tax" : "Submit"}<span><BiRightArrowAlt className="inline font-bold text-xl" /></span></button>}
                                    </div>
                                </>
                            </div>}
                            <div className='h-20 w-full'></div>

                        </div>


                    </>



                </div>

            </div>

            <Modal
                isOpen={modalIsOpen}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
            >

                <div class="relative bg-white rounded-lg shadow-xl border-2 border-gray-50">
                    <button onClick={closeModal} type="button" class="absolute top-3 right-2.5 text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm p-1.5 ml-auto inline-flex items-center dark:hover:bg-gray-800 dark:hover:text-white" >
                        <svg class="w-5 h-5" fill="currentColor" ><path fill-rule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clip-rule="evenodd"></path></svg>
                    </button>
                    <div class="p-6 text-center">
                        <div className='w-full flex h-10'> <span className='mx-auto'><FiAlertCircle className='' size={30} /></span></div>
                        <h3 class="mb-5 text-lg font-normal  text-gray-500">Upload document first then pay tax</h3>
                        <button type="button" class="text-white bg-red-600 hover:bg-red-800 focus:ring-4 focus:outline-none focus:ring-red-300 dark:focus:ring-red-800 font-medium rounded-lg text-sm inline-flex items-center px-10 py-2.5 text-center mr-2" onClick={closeModal}>
                            OK
                        </button>

                    </div>
                </div>

            </Modal>
        </>
    )
}

export default ViewDemandDetails